.main {
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: auto;
  min-height: 768px;
}

.main__wrapper {
  flex-direction: column;
}

.main__slide--active {
  @media (min-width: @desktop-width) {
    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

    &::before {
      background-color: @black;
      animation-name: maskactive;
      animation-duration: 2.8s;
      transform: translateY(-100%);
      z-index: 600;
    }

    &::after {
      background-image: url("../img/icon/logo-mask.svg");
      background-repeat: no-repeat;
      background-size: 409px 424px;
      background-position: center center;
      animation-name: maskimageactive;
      animation-duration: 1.8s;
      z-index: 600;
      transform: translateY(-100%);
    }
  }
}

.main__title {
  .offset(0 0 20px 0; 0);
  .typography(28px; normal);
  text-align: center;
  text-transform: uppercase;
  color: @white;

  @media (min-width: @tablet-width) {
    margin-bottom: 40px;
    font-size: 50px;
  }
}

.main__subtitle {
  .offset(0 0 47px 0; 0);
  .typography(26px; 28px);
  font-weight: 300;
  text-align: center;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    margin-top: 37px;
    margin-bottom: 47px;
    .typography(40px; 46px);
  }
}

.main__subtitle--management {
  color: @white;
}

.main__subtitle--about {
  color: @white;
}

.main__subtitle--history {
  color: @white;
}
